/* 放回我的班级按钮 */
.back-myclass-btn{
float: right;
width: 100px;
height: 35px;
border: none;
background: url('../img/btn_sprites.png') -1px -1px;

color: #fff;
}
.back-myclass-btn:hover{
background: url('../img/btn_sprites.png') -1px -75px;


}
.back-myclass-btn:active{
background: url('../img/btn_sprites.png') -1px -38px;

}
.back-myclass-btn:focus{
outline: none;

}
